import {
    PublicKey,
    SYSVAR_CLOCK_PUBKEY,
    Transaction,
    TransactionInstruction,
} from '@solana/web3.js';
import ReactGA from 'react-ga';
import BigNumber from 'bignumber.js';
import {
    FEE_PAYER_PUBLIC_KEY,
    METASOL_PROGRAM_ID,
    processTxLayout,
    TOKEN_PROGRAM_ID,
    METASOL_PUBLIC_KEY,
    transferCheckedLayout,
} from "./constants";
import { u64 } from './uint64';
import {
    sendToRelayer,
    getTokenAccounts,
    getAssociatedTokenAddress,
    createAssociatedTokenAccountInstruction,
    calculateFeeInSol,
    getFeeInToken,
} from './api';

export const getChainId = (network) => {
    let chainId = 0;

    switch (network) {
        case "mainnet-beta":
            chainId = 101;
            break;
        case "testnet":
            chainId = 102;
            break;
        case "devnet":
            chainId = 103;
            break;
        default:
            chainId = 102;
            break;
    }

    return chainId;
}

export const signAndSendTransaction = (
    connection,
    solletWallet,
    recipient,
    transferAmount,
    selectedTokenData,
    selectedFeeTokenData,
) => {
    return new Promise(async (resolve, reject) => {
        try {
            const senderSplAccount = await getAssociatedTokenAddress(
                selectedTokenData.address, solletWallet.publicKey.toString(),
            );
            const recipientSplAccount = await getAssociatedTokenAddress(
                selectedTokenData.address, recipient,
            );

            const mintPublicKey = new PublicKey(selectedTokenData.address);

            const amount = Number(
                new BigNumber(transferAmount).times(
                    new BigNumber(10).exponentiatedBy(
                        new BigNumber(selectedTokenData.decimals)
                    )
                )
            );

            const transferInstruction = createTransferCheckedInstruction(
                senderSplAccount,
                mintPublicKey,
                recipientSplAccount,
                solletWallet.publicKey,
                [],
                amount,
                selectedTokenData.decimals
            );

            const recentBlock = await connection.getRecentBlockhash();
            const tempTransaction = new Transaction({
                feePayer: FEE_PAYER_PUBLIC_KEY,
                recentBlockhash: recentBlock.blockhash
            });

            if (
                !await isSplAccountExists(
                    connection,
                    selectedTokenData.address,
                    recipient
                )
            ) {
                const createInstruction =
                    createAssociatedTokenAccountInstruction(
                        mintPublicKey,
                        recipientSplAccount,
                        new PublicKey(recipient),
                        FEE_PAYER_PUBLIC_KEY,
                    );

                tempTransaction.add(createInstruction);
            }

            const transaction = await createMetasolTransaction(
                connection,
                tempTransaction,
                transferInstruction,
                solletWallet.publicKey,
                selectedFeeTokenData
            );

            const signedTx = await solletWallet.signTransaction(transaction);

            const bufferTx = signedTx.serialize({
                verifySignatures: false
            });

            const result = await sendToRelayer(bufferTx);
            resolve(result);

            ReactGA.event({
                category: 'Transfer',
                action: 'Executed',
                label: amount.toString(),
            });
        } catch (error) {
            reject(error);
        }
    });
}

const isSplAccountExists = (connection, mint, address) => {
    return new Promise(async (resolve) => {
        const splAccounts =
            await getTokenAccounts(
                connection, mint, address
            );

        resolve(splAccounts.length !== 0);
    })
}

const createMetasolTransaction = async (
    connection,
    transaction,
    txInstruction,
    senderAuthority,
    feeSplToken,
) => {
    const feeSplTokenAddress = feeSplToken.address;
    const senderSplAccount = await getAssociatedTokenAddress(
        feeSplTokenAddress, senderAuthority.toString(),
    );
    const feePayerSplAccount = await getAssociatedTokenAddress(
        feeSplTokenAddress, FEE_PAYER_PUBLIC_KEY.toString(),
    );

    const feeTokenPublicKey = new PublicKey(feeSplTokenAddress);

    const metasolKeys = [
        { pubkey: txInstruction.programId, isSigner: false, isWritable: false },
        { pubkey: METASOL_PUBLIC_KEY, isSigner: false, isWritable: true },

        { pubkey: SYSVAR_CLOCK_PUBKEY, isSigner: false, isWritable: false },

        { pubkey: senderSplAccount, isSigner: false, isWritable: true },
        { pubkey: senderAuthority, isSigner: false, isWritable: false },
        { pubkey: feePayerSplAccount, isSigner: false, isWritable: true },

        { pubkey: feeTokenPublicKey, isSigner: false, isWritable: false },
        { pubkey: TOKEN_PROGRAM_ID, isSigner: false, isWritable: false },
    ];

    const keys = txInstruction.keys.concat(metasolKeys);

    const transactionFee = await calculateFeeInSol(connection, 2);

    const txFee = await getFeeInToken(
        feeSplToken.extensions.coingeckoId,
        feeSplToken.decimals,
        transactionFee,
    );

    const slot = await connection.getSlot();

    const data = Buffer.alloc(48);
    processTxLayout.encode(
        {
            instruction: 1,
            fee: new u64(txFee.amount).toBuffer(),
            expiry_block: new u64(slot + 100).toBuffer(),
            required_accounts_length: txInstruction.keys.length,
            instruction_data: txInstruction.data,
        },
        data,
    );

    if (
        !await isSplAccountExists(
            connection,
            feeSplTokenAddress,
            FEE_PAYER_PUBLIC_KEY.toString()
        )
    ) {
        const feePayerAccountInstruction =
            createAssociatedTokenAccountInstruction(
                feeTokenPublicKey, // Token mint key
                feePayerSplAccount, // Token account
                FEE_PAYER_PUBLIC_KEY, // Owner
                FEE_PAYER_PUBLIC_KEY, // Payer
            );

        transaction.add(feePayerAccountInstruction);
    }

    const processTxInstruction = new TransactionInstruction({
        keys,
        programId: METASOL_PROGRAM_ID,
        data,
    });

    transaction.add(processTxInstruction);

    return transaction;
}

function createTransferCheckedInstruction(
    source,
    mint,
    destination,
    owner,
    multiSigners,
    amount,
    decimals,
) {
    const data = Buffer.alloc(transferCheckedLayout.span);

    transferCheckedLayout.encode(
        {
            instruction: 12, // TransferChecked instruction
            amount: new u64(amount).toBuffer(),
            decimals,
        },
        data,
    );

    let keys = [
        { pubkey: source, isSigner: false, isWritable: true },
        { pubkey: mint, isSigner: false, isWritable: false },
        { pubkey: destination, isSigner: false, isWritable: true },
    ];
    if (multiSigners.length === 0) {
        keys.push({
            pubkey: owner,
            isSigner: true,
            isWritable: false,
        });
    } else {
        keys.push({ pubkey: owner, isSigner: false, isWritable: false });
        multiSigners.forEach((signer) =>
            keys.push({
                pubkey: signer.publicKey,
                isSigner: true,
                isWritable: false,
            }),
        );
    }

    return new TransactionInstruction({
        keys,
        programId: TOKEN_PROGRAM_ID,
        data,
    });
}
