import React, { useEffect, useState } from 'react';
import clsx from 'clsx';
import styles from '../../scss/DarkModeSwitch.module.scss';
import LightModeIcon from '../icons/LightModeIcon';
import DarkModeIcon from '../icons/DarkModeIcon';

function DarkModeSwitch() {
    const [mode, setMode] = useState('light');

    const handleDarkMode = () => {
        const currentMode = window.localStorage.getItem('mode');
        const newMode = currentMode === 'light' ? 'dark' : 'light';
        updateScss(newMode);
        setMode(newMode);
        window.localStorage.setItem('mode', newMode);
    };

    const updateScss = (mode) => {
        const root = document.documentElement;
        if (mode === "light") {
            document.body.classList.add(`light-mode`);
        } else {
            document.body.classList.remove(`light-mode`);
        }
        root.style.setProperty(
            '--sol-balance-background',
            mode === "light" ? '#e6f5ff' : 'black'
        );
        root.style.setProperty(
            '--input-panel-border',
            mode === "light" ? '#F0F0FF' : '#222121'
        );
    }

    useEffect(() => {
        let currentMode = window.localStorage.getItem('mode');
        updateScss(currentMode);
        setMode(currentMode);
    }, [mode]);

    return (
        <button type="button" className={clsx(styles.root, { [styles.darkModeEnabled]: mode !== 'light' })} onClick={handleDarkMode}>
            <div className={styles.track} />
            <div className={styles.knob}>
                <div className={styles.lightModeIcon}>
                    <LightModeIcon />
                </div>
                <div className={styles.darkModeIcon}>
                    <DarkModeIcon />
                </div>
            </div>
        </button>
    );
}

export default DarkModeSwitch;