const uint64Layout = (property = 'uint64') => {
    return BufferLayout.blob(8, property);
}

export const processTxLayout = BufferLayout.struct([
    BufferLayout.u8('instruction'),
    uint64Layout('fee'),
    uint64Layout('expiry_block'),
    BufferLayout.u8('required_accounts_length'),
    vecU8('instruction_data'),
]);

export const transferCheckedLayout = BufferLayout.struct([
    BufferLayout.u8('instruction'),
    uint64Layout('amount'),
    BufferLayout.u8('decimals'),
]);
