import { React, useEffect, useState } from 'react'
import styled from 'styled-components'
import { AutoColumn } from '../Column'
import NumericInput from '../NumericInput'
import { RowBetween } from '../Row'
import { TokenListProvider } from '@solana/spl-token-registry';
import { Dropdown, Image, DropdownButton } from 'react-bootstrap'
import { getChainId } from '../../utils/mics'
import { network } from '../../utils/constants'
import {
    getTokenBalance,
    getTokenAccounts,
    getAssociatedTokenAddress,
    getAmountInUsd,
} from '../../utils/api'
const defaultURI = "https://cryptologos.cc/logos/solana-sol-logo.png";

const LabelRow = styled.div`
  display: flex;
  flex-flow: row-reverse wrap;
  align-items: center;
  span:hover {
    cursor: pointer;
    color: gray;
  }
`

export default function AmountInputPanel({
    connection,
    account,
    value,
    onChangeAmount,
    onSelectToken,
    onTokenBalance,
    onSetShowCreateAccount,
    onError,
    updateBalance,
    updatedAddress,
    setBalanceUpdated,
}) {
    const [allTokens, setAllTokens] = useState([
        {
            decimals: 9,
            symbol: "TestSRM",
            address: "Cv7DvSt9842MSxX8LXQewpfcWNC8qT5P9w7wpHshcEzx",
            extensions: {
                coingeckoId: "serum"
            },
            logoURI: "https://cdn.jsdelivr.net/gh/trustwallet/assets@master/blockchains/ethereum/assets/0x476c5E26a75bd202a9683ffD34359C0CC15be0fF/logo.png",
        },
        {
            decimals: 9,
            symbol: "TestUSDC",
            address: "PRgdpShReV7dRRcXEYBueAHVtCoBAe9kDMUDEYLUR4p",
            extensions: {
                coingeckoId: "usd-coin"
            },
            logoURI: "https://cdn.jsdelivr.net/gh/solana-labs/token-list@main/assets/mainnet/EPjFWdd5AufqSSqeM2qN1xzybapC8G4wEGGkZwyTDt1v/logo.png",
        },
    ]);

    // const [openSelectTokenModal, setOpenSelectTokenModal] = useState(false);
    const [inputAmount, setInputAmount] = useState("");
    const [amountInUsd, setAmountInUsd] = useState("");
    const [selectedCurrencyBalance, setSelectedCurrencyBalance] = useState("");
    const [selectedTokenSymbol, setSelectedTokenSymbol] = useState("Select a token");
    const [selectedTokenCoingeckoId, setSelectedCoingeckoId] = useState("");

    const handleSelectToken = async (event, isUpdate) => {
        // handleModalClose();

        const data = allTokens.filter((element) => {
            return element.address === event
        });
        const tokenInfo = data[0];

        onSelectToken(data[0]);
        setSelectedTokenSymbol(data[0].symbol);
        setSelectedCoingeckoId(data[0].extensions.coingeckoId);

        if (account) {
            const addresses = await getTokenAccounts(
                connection, tokenInfo.address, account
            );

            if (addresses && addresses.length > 0) {
                const tokenAccount = await getAssociatedTokenAddress(
                    tokenInfo.address, account
                );

                const balance = await getTokenBalance(
                    connection, tokenAccount
                );

                setSelectedCurrencyBalance(balance.value.uiAmount);
                onTokenBalance(balance.value.uiAmount);

                if (inputAmount && !isUpdate) {
                    handleOnChangeAmount(
                        inputAmount,
                        data[0].extensions.coingeckoId
                    );
                } else if (isUpdate) {
                    handleOnChangeAmount("");
                }
            } else {
                onSetShowCreateAccount(data[0].address);
                onError("You don't have token account for this token.")
                setSelectedCurrencyBalance("");
            }
        }
    }

    const fetchTokenList = () => {
        const chainId = getChainId(network);
        new TokenListProvider().resolve().then(async (tokens) => {
            const tokenList = tokens.filterByChainId(chainId).getList();
            setAllTokens(allTokens.concat(tokenList));
        });
    }

    const onClickMax = () => {
        handleOnChangeAmount(selectedCurrencyBalance);
    }

    const handleOnChangeAmount = async (amount, coingeckoId) => {
        onChangeAmount(amount);
        setInputAmount(amount);

        coingeckoId = coingeckoId || selectedTokenCoingeckoId;

        if (!amount) {
            setAmountInUsd("");
        } else if (coingeckoId) {
            setAmountInUsd(await getAmountInUsd(
                coingeckoId,
                amount,
            ));
        }
    };

    // const handleModalClose = () => {
    //     setOpenSelectTokenModal(false);
    // }

    useEffect(() => {
        if (allTokens.length === 0) {
            fetchTokenList();
        }

        if (updateBalance) {
            handleSelectToken(updatedAddress, true);
            setBalanceUpdated();
        }
        // eslint-disable-next-line
    }, [updateBalance]);

    return (
        <div id="mainInput">
            <div className="input-container">
                <AutoColumn gap="md">
                    <LabelRow>
                        <RowBetween>
                            <div
                                fontWeight={500}
                                fontSize={14}
                            >
                                Amount
                            </div>

                            {account && (
                                <div
                                    onClick={onClickMax}
                                    color="rgb(86, 90, 105)"
                                    fontWeight={500}
                                    fontSize={14}
                                    style={{ display: 'inline', cursor: 'pointer' }}
                                >
                                    {selectedCurrencyBalance !== ""
                                        ? 'Balance: ' + selectedCurrencyBalance.toFixed(3)
                                        : ' -'
                                    }
                                </div>
                            )}
                        </RowBetween>
                    </LabelRow>

                    <LabelRow>
                        <RowBetween>
                            <NumericInput
                                value={value}
                                onChange={(value) => handleOnChangeAmount(value)}
                            />

                            <DropdownButton
                                title={selectedTokenSymbol}
                                onSelect={(e) => handleSelectToken(e, false)}
                            >
                                {allTokens.map((element, key) => (
                                    element.symbol !== "SOL" ? (
                                        <Dropdown.Item
                                            key={key}
                                            eventKey={element.address}
                                        >
                                            <Image
                                                width="20px"
                                                src={
                                                    element.logoURI ?
                                                        element.logoURI :
                                                        defaultURI
                                                }
                                                style={{ marginRight: "20px" }}

                                            />
                                            {element.symbol}
                                        </Dropdown.Item>
                                    ) : null
                                ))}
                            </DropdownButton>

                            {/* <DropdownButton
                                title={selectedTokenSymbol}
                                onClick={() => setOpenSelectTokenModal(true)}
                            >

                            </DropdownButton>

                            <Modal
                                show={openSelectTokenModal}
                                onHide={handleModalClose}
                                animation={false}
                                className="select-token-modal"
                            >
                                <Modal.Header closeButton>
                                    <Modal.Title>Choose Token</Modal.Title>
                                </Modal.Header>
                                <Modal.Body>
                                    {allTokens.map((element) => (
                                        element.symbol !== "SOL" ? (
                                            <div
                                                className="modal-div"
                                                key={element.address}
                                                onClick={() => handleSelectToken(element.address)}
                                            >
                                                <Image
                                                    width="20px"
                                                    src={
                                                        element.logoURI ?
                                                            element.logoURI :
                                                            defaultURI
                                                    }
                                                    style={{ marginRight: "20px" }}

                                                />
                                                <span
                                                    style={{ fontSize: "20px" }}
                                                >
                                                    {element.symbol}
                                                </span>
                                            </div>
                                        ) : null
                                    ))}
                                </Modal.Body>
                            </Modal> */}
                        </RowBetween>
                    </LabelRow>

                    <LabelRow>
                        <RowBetween>
                            <div>
                                {amountInUsd ?
                                    <span>
                                        $
                                        <span> {amountInUsd}</span>
                                    </span>
                                    : null
                                }
                            </div>
                        </RowBetween>
                    </LabelRow>
                </AutoColumn>
            </div>
        </div >
    )
}