import React, { useEffect, useState } from 'react';
import { Image, Modal } from 'react-bootstrap';
import { SolletAdapter } from './Sollet';
import { PhantomAdapter } from './Phantom';

export default function Connect({
    onConnected,
    onDisconnected,
    onClose,
    onError,
}) {
    const [availableAdapters, setAvailableAdapters] = useState([
        {
            key: 0, name: 'Sollet', isInstalled: true,
            logo: "https://cdn.jsdelivr.net/gh/solana-labs/oyster@main/assets/wallets/sollet.svg"
        },
        {
            key: 1, name: "Phantom", isInstalled: false,
            logo: "https://phantom.app/img/logo.png"
        },
    ]);

    const handleConnectWallet = async (event) => {
        try {
            handleCloseModal();

            const walletAdpater = getWalletAdapter(event);

            const wallet = await walletAdpater.connect();

            if (walletAdpater.isInstalled) {
                wallet.on('connect', async (_) => {
                    onConnected(wallet);
                });

                wallet.on('disconnect', () => {
                    onDisconnected();
                });
            }
        } catch (error) {
            onError(error);
        }
    }

    const getWalletAdapter = (selectedAdapter) => {
        switch (Number(selectedAdapter)) {
            case 0:
                return new SolletAdapter();
            case 1:
                return new PhantomAdapter();
            default:
                break;
        }
    }

    const updateIsAdapterInstalled = () => {
        let tempAdapters = [...availableAdapters];

        for (let i = 1; i <= availableAdapters.length - 1; i++) {
            const adapter = getWalletAdapter(i);
            let adapterDetail = {
                ...tempAdapters[1],
                isInstalled: adapter.isInstalled || false
            }

            tempAdapters[i] = adapterDetail;

            if (i === availableAdapters.length - 1) {
                setAvailableAdapters(tempAdapters);
            }
        }
    }

    const handleCloseModal = () => {
        onClose();
    }

    useEffect(() => {
        updateIsAdapterInstalled();
        // eslint-disable-next-line
    }, []);

    return (
        <>
            <Modal show={true} onHide={handleCloseModal} animation={false} >
                <Modal.Header closeButton>
                    <Modal.Title>Connect Your Wallet</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {availableAdapters.map((element) => (
                        <div
                            className="modal-div"
                            key={element.key}
                            onClick={() => handleConnectWallet(element.key)}
                        >
                            <Image
                                width="20px"
                                src={element.logo}
                                style={{ marginRight: "1rem" }}
                            />
                            {!element.isInstalled ?
                                <span>Install </span> : null
                            }
                            {element.name}
                        </div>
                    ))}
                </Modal.Body>
            </Modal>
        </>
    );
}
