import {
    LAMPORTS_PER_SOL,
    PublicKey,
    SystemProgram,
    SYSVAR_RENT_PUBKEY,
    Transaction,
    TransactionInstruction,
} from "@solana/web3.js";
import axios from "axios";
import ReactGA from 'react-ga';
import BigNumber from "bignumber.js";
import {
    relayerUrl,
    coingeckoApi,
    TOKEN_PROGRAM_ID,
    ASSOCIATED_TOKEN_PROGRAM_ID,
} from "./constants";

export const getSolBalance = async (connection, account) => {
    try {
        let balance = await connection.getBalance(account);
        return {
            balance,
            uiBalance: balance / Math.pow(10, 9),
        }
    } catch (error) {
        console.log(error);
    }
};

export const getTokenAccounts = async (connection, mint, owner) => {
    try {
        let accounts = await connection.getTokenAccountsByOwner(
            new PublicKey(owner),
            { mint: new PublicKey(mint) },
        );

        return accounts.value;
    } catch (error) {
        console.log(error);
    }
}

export const getTokenBalance = async (connection, tokenAccount) => {
    try {
        return await connection.getTokenAccountBalance(tokenAccount);
    } catch (error) {
        console.log(error);
    }
};

export const calculateFeeInSol = async (
    connection,
    totalSigners,
) => {
    const recentBlock = await connection.getRecentBlockhash();

    const feeCalulator = await connection
        .getFeeCalculatorForBlockhash(recentBlock.blockhash);

    const lamportsPerSignature = feeCalulator.value
        .lamportsPerSignature;

    return lamportsPerSignature * totalSigners;
}

export const getFeeInToken = async (
    name,
    decimals,
    feeInLamport,
) => {
    const result = await getTokenAndSolPrice(name);
    const tokenPrice = result.tokenPriceInSol;
    const solPriceInUsd = result.solPriceInUsd;

    const oneLamportInSol = new BigNumber(1).dividedBy(LAMPORTS_PER_SOL);

    const feeInSol = new BigNumber(feeInLamport).times(oneLamportInSol);
    const feeInToken = tokenPrice.times(feeInSol);
    const feeInUsd = feeInSol.times(solPriceInUsd);

    return {
        amount: feeInToken.times
            (
                new BigNumber(10)
                    .exponentiatedBy(decimals)
            )
            .toFixed(0),
        uiAmount: feeInToken,
        uiAmountUsd: feeInUsd.toFixed(4),
    };
}

const getTokenAndSolPrice = async (name) => {
    const currentSolPrice = await getCurrentSolPrice();

    const tokenPriceInUsd = await axios.get(
        `${coingeckoApi}/simple/price?ids=${name}&vs_currencies=usd`
    );

    const usdPriceInToken = new BigNumber(1)
        .dividedBy(new BigNumber(tokenPriceInUsd.data[name].usd));

    return {
        solPriceInUsd: currentSolPrice,
        tokenPriceInSol: usdPriceInToken.times(currentSolPrice)
    };
};

const getCurrentSolPrice = async () => {
    const res = await axios.get(
        `${coingeckoApi}/simple/price?ids=solana&vs_currencies=usd`
    );

    return new BigNumber(res.data.solana.usd);
};

export const getAmountInUsd = async (name, amount) => {
    const res = await axios.get(
        `${coingeckoApi}/simple/price?ids=${name}&vs_currencies=usd`
    );

    const tokenPriceInUsd = new BigNumber(res.data[name].usd);
    const amountInUsd = new BigNumber(amount).times(tokenPriceInUsd);

    return amountInUsd.toFixed(4);
};

export const sendToRelayer = (bufferTx) => {
    return new Promise((resolve, reject) => {
        axios.post
            (
                `${relayerUrl}/execute-tx`,
                {
                    bufferTx
                }
            )
            .then((txSignature) => {
                resolve(txSignature)
            })
            .catch((error) => {
                reject(error);
            })
    });
};

export const createAndSendTestTokens = (
    mintAddress,
    ownerAddress,
) => {
    return new Promise((resolve, reject) => {
        axios.post
            (
                `${relayerUrl}/create-wallet`,
                {
                    mintAddress,
                    ownerAddress,
                }
            )
            .then((result) => {
                resolve(result)

                ReactGA.event({
                    category: 'Wallet',
                    action: 'Created',
                });
            })
            .catch((error) => {
                reject(error);
            })
    });
};

export const getAssociatedTokenAddress = async (mint, owner) => {
    try {
        mint = new PublicKey(mint);
        owner = new PublicKey(owner);

        return (
            await PublicKey.findProgramAddress(
                [owner.toBuffer(), TOKEN_PROGRAM_ID.toBuffer(), mint.toBuffer()],
                ASSOCIATED_TOKEN_PROGRAM_ID,
            )
        )[0];
    } catch (error) {
        console.log(error);
    }
}

export async function createAssociatedTokenAccount(
    connection,
    owner,
    associatedAddress,
    mint,
    payer
) {
    try {
        await connection.sendAndConfirmTransaction(
            'CreateAssociatedTokenAccount',
            connection,
            new Transaction().add(
                createAssociatedTokenAccountInstruction(
                    mint,
                    associatedAddress,
                    owner,
                    payer.publicKey,
                ),
            ),
            payer,
        );

        return true;
    } catch (error) {
        console.log(error);
    }
}

/**
 * Construct the AssociatedTokenProgram instruction to create the associated
 * token account
 *
 * @param associatedProgramId SPL Associated Token program account
 * @param programId SPL Token program account
 * @param mint Token mint account
 * @param associatedAccount New associated account
 * @param owner Owner of the new account
 * @param payer Payer of fees
 */
export function createAssociatedTokenAccountInstruction(
    mint,
    associatedAccount,
    owner,
    payer,
) {
    const data = Buffer.alloc(0);

    let keys = [
        { pubkey: payer, isSigner: true, isWritable: true },
        { pubkey: associatedAccount, isSigner: false, isWritable: true },
        { pubkey: owner, isSigner: false, isWritable: false },
        { pubkey: mint, isSigner: false, isWritable: false },
        { pubkey: SystemProgram.programId, isSigner: false, isWritable: false },
        { pubkey: TOKEN_PROGRAM_ID, isSigner: false, isWritable: false },
        { pubkey: SYSVAR_RENT_PUBKEY, isSigner: false, isWritable: false },
    ];

    return new TransactionInstruction({
        keys,
        programId: ASSOCIATED_TOKEN_PROGRAM_ID,
        data,
    });
}
