import React, { useState } from 'react';
import BigNumber from 'bignumber.js';
import { Box } from '@chakra-ui/layout';
import { Connection, clusterApiUrl } from '@solana/web3.js';
import { Card, Row, Col, Button, Image } from 'react-bootstrap';
import {
    network,
    devnetUrl,
    localnetUrl,
    explorerLink,
} from '../../utils/constants';
import Connect from '../Connect';
import { AutoColumn } from '../Column';
import SocialLinks from '../SocialLinks';
import FeeInputPanel from '../FeeInputPanel';
import AmountInputPanel from '../AmountInputPanel';
import AddressInputPanel from '../AddressInputPanel';
import { signAndSendTransaction, } from '../../utils/mics';
import {
    getSolBalance,
    createAndSendTestTokens,
} from '../../utils/api';
import SolanaLogo from '../../assets/solana-logo.png';
import DarkModeSwitch from '../DarkModeSwitch/DarkModeSwitch';

export default function Main() {
    const [connection] = useState(
        new Connection(
            network === "devnet" ?
                devnetUrl :
                (network === "localnet" ?
                    localnetUrl :
                    clusterApiUrl(network)
                )
        )
    );

    const [error, setError] = useState("");
    const [message, setMessage] = useState("");

    const [amount, setAmount] = useState("");
    const [recipient, setRecipient] = useState("");
    const [solBalance, setSolBalance] = useState("");
    const [tokenBalance, setTokenBalance] = useState("");
    const [solletWallet, setSollectWallet] = useState();

    const [isConnected, setIsConnected] = useState(false);
    const [walletAddress, setWalletAddress] = useState("");
    const [selectedTokenData, setSelectedTokenData] = useState({});
    const [showCreateAccount, setShowCreateAccount] = useState(false);
    const [creatingAccount, setCreatingAccount] = useState(false);
    const [executing, setExecuting] = useState(false);
    const [updateBalance, setUpdateBalance] = useState(false);
    const [txSignature, setTxSignature] = useState("");
    const [openConnectModal, setOpenConnectModal] = useState(false);
    const [transactionFee, setTransactionFee] = useState("");
    const [feeTokenBalance, setFeeTokenBalance] = useState("");
    const [selectedFeeTokenData, setSelectedFeeTokenData] = useState({});
    const [accountToCreate, setAccountToCreate] = useState("");

    const handleValidateAndTransfer = async () => {
        let errorMsg = "";

        if (!recipient && recipient.length !== 44) {
            errorMsg = "Invalid recipient address.";
        } else if (!selectedTokenData || !selectedTokenData.address) {
            errorMsg = "Please select the token.";
        } else if (amount === "") {
            errorMsg = "Amount can not be null.";
        } else if (new BigNumber(amount).gt(new BigNumber(tokenBalance))) {
            errorMsg = "Amount should not be greater than available balance.";
        } else if (!selectedFeeTokenData || !selectedFeeTokenData.address) {
            errorMsg = "Please select the token.";
        } else if (transactionFee.gt(new BigNumber(feeTokenBalance))) {
            errorMsg = "Insufficient balance to cover transaction fee.";
        } else if (
            selectedTokenData.address === selectedFeeTokenData.address &&
            new BigNumber(amount).plus(transactionFee).gt(new BigNumber(tokenBalance))
        ) {
            errorMsg = "Insufficient balance to cover transaction fee.";
        } else {
            handleTransferSplToken();
        }

        handleError(errorMsg);
    }

    const handleTransferSplToken = () => {
        setExecuting(true);

        signAndSendTransaction(
            connection,
            solletWallet,
            recipient,
            amount,
            selectedTokenData,
            selectedFeeTokenData,
        )
            .then((result) => {
                setExecuting(false);
                handleMesage(
                    "Transfer Submitted ",
                    result.data.txSignature,
                    false,
                );

                let interval = setInterval(async () => {
                    let status = await connection.getSignatureStatus(
                        result.data.txSignature
                    );

                    if (status.value?.confirmationStatus === "finalized") {
                        setUpdateBalance(true);
                        setAmount("");
                        setMessage("");
                        clearInterval(interval);
                    }
                }, 1000)
            })
            .catch((error) => {
                setExecuting(false);
                handleError(error.message);
            });
    }

    const handleCreateAccount = () => {
        setCreatingAccount(true);

        createAndSendTestTokens
            (
                accountToCreate,
                walletAddress
            )
            .then((result) => {
                setShowCreateAccount(false);
                setCreatingAccount(false);
                handleMesage(
                    "Account created successfully ",
                    result.data.txSignature,
                    false,
                );

                let interval = setInterval(async () => {
                    let status = await connection.getSignatureStatus(
                        result.data.txSignature
                    );

                    console.log(status.value?.confirmationStatus)
                    if (status.value?.confirmationStatus === "finalized") {
                        if (accountToCreate === selectedTokenData.address) {
                            setUpdateBalance(true);
                        }
                        setAccountToCreate("");
                        setMessage("");
                        clearInterval(interval);
                    }
                }, 1000)
            })
            .catch((error) => {
                setCreatingAccount(false);
                handleError(error.message);
            });
    }

    const handleChangeRecipient = (value) => {
        setRecipient(value)
    }

    const handleChangeAmount = (value) => {
        setAmount(value)
    }

    const handleSelectToken = (tokenData) => {
        setSelectedTokenData(tokenData);
    }

    const handleSetTokenBalance = (value) => {
        setTokenBalance(value);
        setShowCreateAccount(false);
    }

    const handleSelectFeeToken = (tokenData) => {
        setSelectedFeeTokenData(tokenData);
    }

    const handleSetFeeTokenBalance = (value) => {
        setFeeTokenBalance(value);
    }

    const handleOpenConnectModal = () => {
        if (!isConnected)
            setOpenConnectModal(true);
    }

    const handleTransactionFee = (value) => {
        setTransactionFee(value);
    }

    const handleOnWalletConnect = async (wallet) => {
        setIsConnected(true);
        setSollectWallet(wallet);
        const publicKey = wallet.publicKey;
        setWalletAddress(publicKey.toString());
        console.log('Connected to ' + publicKey.toBase58())

        const sol = await getSolBalance(connection, publicKey);
        if (sol) {
            setSolBalance(sol.uiBalance);
        }

        if (selectedTokenData && selectedTokenData.address) {
            setUpdateBalance(true);
        }
    }

    const handleOnWalletDisconnect = (wallet) => {
        setIsConnected(false);
        console.log('Wallet Disconnected');
    }

    const handleError = (message) => {
        setError(message);

        setTimeout(() => {
            setError("");
        }, 4000);
    }

    const handleMesage = (
        message,
        signature,
        enableTimeout,
    ) => {
        setMessage(message);

        if (signature) {
            setTxSignature(signature);
        }

        if (enableTimeout) {
            setTimeout(() => {
                setMessage("");
            }, !signature ? 4000 : 10000);
        }
    }

    return (
        <main className="homepage">
            {/* Navigation*/}
            <nav className="navbar navbar-expand-lg navbar-light py-3" id="mainNav">
                <div className="container" style={{ marginTop: "0.5rem" }}>
                    <div className="js-scroll-trigger logo"></div>

                    <div>
                        <ul className="navbar-nav ml-auto my-2 my-lg-0">
                            {isConnected && solBalance !== "" ?
                                <li className="nav-item">
                                    <div className="sol-balance">
                                        <span style={{ marginRight: "5px" }}>
                                            {solBalance.toFixed(2)}
                                        </span>
                                        <Image width="20px" src={SolanaLogo}></Image>
                                    </div>
                                </li>
                                : null
                            }

                            <li className="nav-item">
                                <DarkModeSwitch />
                            </li>

                            <li className="nav-item">
                                <Button
                                    variant="outline-success nav-button"
                                    onClick={handleOpenConnectModal}
                                >
                                    {!isConnected ?
                                        <span>CONNECT</span> :
                                        <a
                                            className="nav-link"
                                            href={`${explorerLink}/address/${walletAddress}?cluster=${network}`}
                                            target="_blank"
                                            rel="noopener noreferrer"
                                        >
                                            {walletAddress.substr(0, 4)}
                                            ...
                                            {walletAddress.substr(
                                                walletAddress.length - 4,
                                                walletAddress.length - 1,
                                            )}
                                        </a>
                                    }
                                </Button>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>

            {/* Main*/}
            <header className="homepage" id="">
                <div className="container h-100">
                    <div className="row h-100 align-items-center justify-content-center text-center">
                        <Card>
                            <Card.Body>
                                <AutoColumn gap="20px">
                                    <AddressInputPanel
                                        id="recipient"
                                        value={recipient}
                                        onChange={handleChangeRecipient}
                                    />

                                    <AmountInputPanel
                                        connection={connection}
                                        account={walletAddress}
                                        value={amount}
                                        onChangeAmount={handleChangeAmount}
                                        onSelectToken={handleSelectToken}
                                        onTokenBalance={handleSetTokenBalance}
                                        onSetShowCreateAccount={
                                            (address) => {
                                                setTokenBalance("");
                                                setAccountToCreate(address);
                                                setShowCreateAccount(true)
                                            }
                                        }
                                        onError={handleError}
                                        updateBalance={updateBalance}
                                        updatedAddress={selectedTokenData.address}
                                        setBalanceUpdated={() => setUpdateBalance(false)}
                                    />

                                    <FeeInputPanel
                                        connection={connection}
                                        account={walletAddress}
                                        onSelectToken={handleSelectFeeToken}
                                        onTokenBalance={handleSetFeeTokenBalance}
                                        onTransactionFee={handleTransactionFee}
                                        onError={handleError}
                                        recipient={recipient}
                                        amount={amount}
                                        tokenAddress={selectedTokenData.address}
                                        onSetShowCreateAccount={
                                            (address) => {
                                                setTokenBalance("");
                                                setAccountToCreate(address);
                                                setShowCreateAccount(true)
                                            }
                                        }
                                    />

                                    {error ?
                                        <div className="error-msg">
                                            {error}
                                        </div>
                                        : null
                                    }

                                    {message ?
                                        <div className="message">
                                            {message}
                                            <div>
                                                {txSignature ?
                                                    <div>
                                                        <a
                                                            href={`${explorerLink}/tx/${txSignature}?cluster=${network}`}
                                                            target="_blank"
                                                            rel="noopener noreferrer"
                                                        >
                                                            <strong>Click here</strong>
                                                        </a>
                                                        <span> to see tx execution on the solana explorer.</span>
                                                    </div>
                                                    : null
                                                }
                                            </div>
                                        </div>
                                        : null
                                    }

                                    <Row className="text-center">
                                        <Col>
                                            {isConnected ? (
                                                showCreateAccount ?
                                                    < Button
                                                        onClick={handleCreateAccount}
                                                        variant="outline-success"
                                                    >
                                                        {creatingAccount ?
                                                            <div className="d-flex align-items-center">
                                                                Creating Account
                                                            <span className="loading ml-2"></span>
                                                            </div>
                                                            :
                                                            <div>Create Account</div>
                                                        }
                                                    </Button>
                                                    :
                                                    <Button
                                                        onClick={handleValidateAndTransfer}
                                                        variant="outline-success"
                                                        disabled={
                                                            showCreateAccount &&
                                                            selectedTokenData.symbol !== "MetaSPL"
                                                        }
                                                    >
                                                        {executing ?
                                                            <div className="d-flex align-items-center">
                                                                Executing
                                                            <span className="loading ml-2"></span>
                                                            </div>
                                                            :
                                                            <div>Submit</div>
                                                        }
                                                    </Button>
                                            ) :
                                                <Button
                                                    onClick={handleOpenConnectModal}
                                                    variant="outline-success"
                                                >
                                                    <div>Connect Wallet</div>
                                                </Button>
                                            }
                                        </Col>
                                    </Row>
                                </AutoColumn>
                            </Card.Body>
                        </Card>
                    </div>
                </div>
            </header>

            {/* Footer*/}
            <footer className="py-5">
                <div className="container">
                    <Box
                        maxW="containerLg"
                        color="white" d="flex"
                        justifyContent="space-between"
                    >
                        <div className="copyright">
                            Copyright  2021 - MetaSol
                        </div>
                        <SocialLinks />
                    </Box>
                </div>
            </footer>

            {openConnectModal ?
                <Connect
                    onConnected={(wallet) => handleOnWalletConnect(wallet)}
                    onDisconnected={handleOnWalletDisconnect}
                    onClose={() => setOpenConnectModal(false)}
                    onError={handleError}
                /> : null
            }
        </main >
    );
}
